import React, { useState, useEffect, useRef } from 'react';
import { PaperAirplaneIcon, ChatBubbleLeftIcon } from '@heroicons/react/24/outline';

const ChatInterface = ({ sessionToken, agent }) => {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef(null);

  // Initialize with welcome message
  useEffect(() => {
    if (agent) {
      setMessages([
        {
          id: 'welcome',
          content: agent.config?.appearance?.welcomeMessage || 'Hello! How can I help you today?',
          sender: 'agent',
          timestamp: new Date(),
          agentName: agent.name
        }
      ]);
    }
  }, [agent]);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (!newMessage.trim() || isLoading) return;

    const userMessage = {
      id: Date.now().toString(),
      content: newMessage.trim(),
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setNewMessage('');
    setIsLoading(true);
    setIsTyping(true);

    try {
      // Mock agent response - in production, call the AgentManager.processAgentChat
      const mockResponses = [
        "I understand your question. Let me help you with that.",
        "That's a great question! Based on my knowledge, I can suggest the following approach.",
        "I'd be happy to assist you with this. Here's what I recommend:",
        "Thanks for reaching out! I can help you resolve this issue.",
        "I see what you're asking about. Let me provide you with the relevant information."
      ];

      // Simulate typing delay
      setTimeout(() => {
        const randomResponse = mockResponses[Math.floor(Math.random() * mockResponses.length)];
        
        const agentResponse = {
          id: (Date.now() + 1).toString(),
          content: randomResponse,
          sender: 'agent',
          timestamp: new Date(),
          agentName: agent.name
        };

        setMessages(prev => [...prev, agentResponse]);
        setIsTyping(false);
        setIsLoading(false);
      }, 1500 + Math.random() * 1000);
      
    } catch (error) {
      console.error('Failed to send message:', error);
      setIsTyping(false);
      setIsLoading(false);
      
      const errorMessage = {
        id: (Date.now() + 1).toString(),
        content: "I'm sorry, I'm having trouble connecting right now. Please try again.",
        sender: 'agent',
        timestamp: new Date(),
        agentName: agent.name,
        isError: true
      };
      
      setMessages(prev => [...prev, errorMessage]);
    }
  };

  const formatTime = (timestamp) => {
    return new Date(timestamp).toLocaleTimeString([], { 
      hour: '2-digit', 
      minute: '2-digit' 
    });
  };

  const getMessageTheme = () => {
    const theme = agent?.config?.appearance?.theme || 'blue';
    const themes = {
      blue: 'bg-blue-500',
      green: 'bg-green-500',
      purple: 'bg-purple-500',
      orange: 'bg-orange-500'
    };
    return themes[theme] || themes.blue;
  };

  return (
    <div className="max-w-4xl mx-auto">
      <div className="bg-white rounded-lg card-shadow overflow-hidden">
        {/* Chat Header */}
        <div className={`${getMessageTheme()} px-6 py-4 text-white`}>
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <div className="w-10 h-10 bg-white bg-opacity-20 rounded-full flex items-center justify-center">
                <ChatBubbleLeftIcon className="w-5 h-5" />
              </div>
            </div>
            <div className="ml-4">
              <h2 className="text-lg font-semibold">{agent?.name}</h2>
              <p className="text-sm opacity-90">{agent?.description}</p>
            </div>
          </div>
        </div>

        {/* Chat Messages */}
        <div className="h-96 overflow-y-auto p-6 space-y-4">
          {messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}
            >
              <div
                className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                  message.sender === 'user'
                    ? 'bg-primary-600 text-white'
                    : message.isError
                    ? 'bg-red-100 text-red-800 border border-red-200'
                    : 'bg-gray-100 text-gray-800'
                }`}
              >
                <p className="text-sm">{message.content}</p>
                <div className="flex items-center justify-between mt-1">
                  <span className={`text-xs ${
                    message.sender === 'user' 
                      ? 'text-primary-200' 
                      : message.isError
                      ? 'text-red-600'
                      : 'text-gray-500'
                  }`}>
                    {message.sender === 'agent' && message.agentName && (
                      <span className="mr-2">{message.agentName}</span>
                    )}
                    {formatTime(message.timestamp)}
                  </span>
                </div>
              </div>
            </div>
          ))}
          
          {/* Typing Indicator */}
          {isTyping && (
            <div className="flex justify-start">
              <div className="bg-gray-100 px-4 py-2 rounded-lg">
                <div className="flex space-x-1">
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                </div>
              </div>
            </div>
          )}
          
          <div ref={messagesEndRef} />
        </div>

        {/* Message Input */}
        <div className="border-t border-gray-200 px-6 py-4">
          <form onSubmit={handleSendMessage} className="flex space-x-4">
            <div className="flex-1">
              <input
                type="text"
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
                placeholder="Type your message..."
                disabled={isLoading}
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent disabled:bg-gray-100 disabled:cursor-not-allowed"
              />
            </div>
            <button
              type="submit"
              disabled={!newMessage.trim() || isLoading}
              className={`px-4 py-2 rounded-lg transition-colors ${
                newMessage.trim() && !isLoading
                  ? `${getMessageTheme()} text-white hover:opacity-90`
                  : 'bg-gray-300 text-gray-500 cursor-not-allowed'
              }`}
            >
              <PaperAirplaneIcon className="w-5 h-5" />
            </button>
          </form>
        </div>
      </div>

      {/* Chat Info */}
      <div className="mt-6 bg-gray-50 rounded-lg p-4">
        <h3 className="text-sm font-medium text-gray-900 mb-2">Agent Information</h3>
        <div className="text-sm text-gray-600 space-y-1">
          <div>
            <span className="font-medium">Personality:</span> {agent?.config?.personality?.traits?.join(', ') || 'Not configured'}
          </div>
          <div>
            <span className="font-medium">Tone:</span> {agent?.config?.personality?.tone || 'Professional'}
          </div>
          <div>
            <span className="font-medium">Response Style:</span> {agent?.config?.personality?.responseStyle || 'Helpful'}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChatInterface; 