import React, { useState, useEffect } from 'react';
import { CheckCircleIcon, XCircleIcon, PlayIcon, PauseIcon } from '@heroicons/react/24/outline';

const AgentList = ({ sessionToken, onSelectAgent, selectedAgent }) => {
  const [agents, setAgents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Mock data for development - replace with actual API calls
  useEffect(() => {
    const loadAgents = async () => {
      try {
        setLoading(true);
        
        // Mock agents data - in production, call the AgentManager canister
        const mockAgents = [
          {
            id: 'agent_001',
            name: 'Customer Support Bot',
            description: 'Handles customer inquiries and basic support tasks',
            status: 'Active',
            created: '2024-01-15T10:30:00Z',
            config: {
              personality: { traits: ['helpful', 'professional'] },
              appearance: { avatar: 'default', theme: 'blue' }
            }
          },
          {
            id: 'agent_002', 
            name: 'Sales Assistant',
            description: 'Helps with product recommendations and sales inquiries',
            status: 'Active',
            created: '2024-01-20T16:45:00Z',
            config: {
              personality: { traits: ['enthusiastic', 'knowledgeable'] },
              appearance: { avatar: 'professional', theme: 'green' }
            }
          }
        ];
        
        setAgents(mockAgents);
        setError(null);
      } catch (err) {
        console.error('Failed to load agents:', err);
        setError('Failed to load agents');
      } finally {
        setLoading(false);
      }
    };

    if (sessionToken) {
      loadAgents();
    }
  }, [sessionToken]);

  const toggleAgentStatus = async (agentId) => {
    try {
      // Mock status toggle - in production, call the AgentManager canister
      setAgents(prev => prev.map(agent => 
        agent.id === agentId 
          ? { ...agent, status: agent.status === 'Active' ? 'Inactive' : 'Active' }
          : agent
      ));
    } catch (err) {
      console.error('Failed to toggle agent status:', err);
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <div className="flex">
          <XCircleIcon className="h-5 w-5 text-red-400" />
          <div className="ml-3">
            <p className="text-sm text-red-800">{error}</p>
          </div>
        </div>
      </div>
    );
  }

  if (agents.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="mx-auto h-12 w-12 text-gray-400">
          <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
        </div>
        <h3 className="mt-2 text-sm font-medium text-gray-900">No agents yet</h3>
        <p className="mt-1 text-sm text-gray-500">
          Get started by creating your first AI agent.
        </p>
      </div>
    );
  }

  return (
    <div>
      <div className="mb-6">
        <h2 className="text-lg font-medium text-gray-900">Your AI Agents</h2>
        <p className="text-sm text-gray-600">Manage and monitor your AI-powered support agents</p>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {agents.map((agent) => (
          <div
            key={agent.id}
            className={`bg-white rounded-lg card-shadow p-6 cursor-pointer transition-all hover:shadow-lg ${
              selectedAgent?.id === agent.id ? 'ring-2 ring-primary-500' : ''
            }`}
            onClick={() => onSelectAgent(agent)}
          >
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <h3 className="text-lg font-medium text-gray-900 mb-2">{agent.name}</h3>
                <p className="text-sm text-gray-600 mb-4">{agent.description}</p>
                
                <div className="flex items-center space-x-4 text-sm">
                  <div className="flex items-center">
                    {agent.status === 'Active' ? (
                      <CheckCircleIcon className="h-4 w-4 text-green-500 mr-1" />
                    ) : (
                      <XCircleIcon className="h-4 w-4 text-red-500 mr-1" />
                    )}
                    <span className={agent.status === 'Active' ? 'text-green-700' : 'text-red-700'}>
                      {agent.status}
                    </span>
                  </div>
                  
                  <div className="text-gray-500">
                    Created {new Date(agent.created).toLocaleDateString()}
                  </div>
                </div>
              </div>
              
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  toggleAgentStatus(agent.id);
                }}
                className={`p-2 rounded-full transition-colors ${
                  agent.status === 'Active'
                    ? 'text-orange-600 hover:bg-orange-50'
                    : 'text-green-600 hover:bg-green-50'
                }`}
                title={agent.status === 'Active' ? 'Pause agent' : 'Activate agent'}
              >
                {agent.status === 'Active' ? (
                  <PauseIcon className="h-5 w-5" />
                ) : (
                  <PlayIcon className="h-5 w-5" />
                )}
              </button>
            </div>
            
            <div className="mt-4 pt-4 border-t border-gray-200">
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-500">Personality:</span>
                <div className="flex space-x-1">
                  {agent.config.personality.traits.map((trait, index) => (
                    <span
                      key={index}
                      className="px-2 py-1 bg-primary-100 text-primary-800 rounded-full text-xs"
                    >
                      {trait}
                    </span>
                  ))}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
      
      {selectedAgent && (
        <div className="mt-6 bg-primary-50 border border-primary-200 rounded-md p-4">
          <p className="text-sm text-primary-800">
            <strong>{selectedAgent.name}</strong> is selected. You can now chat with this agent or modify its settings.
          </p>
        </div>
      )}
    </div>
  );
};

export default AgentList; 