import React, { useState } from 'react';
import { CheckIcon, XMarkIcon } from '@heroicons/react/24/outline';

const AgentCreator = ({ sessionToken, onAgentCreated }) => {
  const [activeConfigTab, setActiveConfigTab] = useState('basic');
  const [creating, setCreating] = useState(false);
  const [previewMode, setPreviewMode] = useState(false);
  
  // Form state
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    personality: {
      traits: [],
      tone: 'professional',
      responseStyle: 'helpful'
    },
    knowledgeBase: {
      documents: [],
      sources: [],
      context: ''
    },
    behavior: {
      maxResponseLength: 500,
      conversationMemory: true,
      escalationRules: []
    },
    appearance: {
      avatar: 'default',
      theme: 'blue',
      welcomeMessage: 'Hello! How can I help you today?'
    }
  });

  const configTabs = [
    { id: 'basic', name: 'Basic Info', required: true },
    { id: 'personality', name: 'Personality', required: true },
    { id: 'knowledge', name: 'Knowledge', required: false },
    { id: 'behavior', name: 'Behavior', required: false },
    { id: 'appearance', name: 'Appearance', required: false }
  ];

  const personalityTraits = [
    'helpful', 'professional', 'friendly', 'enthusiastic', 
    'patient', 'knowledgeable', 'empathetic', 'concise'
  ];

  const themes = [
    { id: 'blue', name: 'Professional Blue', color: 'bg-blue-500' },
    { id: 'green', name: 'Success Green', color: 'bg-green-500' },
    { id: 'purple', name: 'Creative Purple', color: 'bg-purple-500' },
    { id: 'orange', name: 'Energetic Orange', color: 'bg-orange-500' }
  ];

  const handleInputChange = (section, field, value) => {
    setFormData(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: value
      }
    }));
  };

  const handleTraitToggle = (trait) => {
    setFormData(prev => ({
      ...prev,
      personality: {
        ...prev.personality,
        traits: prev.personality.traits.includes(trait)
          ? prev.personality.traits.filter(t => t !== trait)
          : [...prev.personality.traits, trait]
      }
    }));
  };

  const handleCreateAgent = async () => {
    try {
      setCreating(true);
      
      // Mock agent creation - in production, call the AgentManager canister
      console.log('Creating agent with data:', formData);
      
      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // Reset form
      setFormData({
        name: '',
        description: '',
        personality: { traits: [], tone: 'professional', responseStyle: 'helpful' },
        knowledgeBase: { documents: [], sources: [], context: '' },
        behavior: { maxResponseLength: 500, conversationMemory: true, escalationRules: [] },
        appearance: { avatar: 'default', theme: 'blue', welcomeMessage: 'Hello! How can I help you today?' }
      });
      
      onAgentCreated();
    } catch (error) {
      console.error('Failed to create agent:', error);
    } finally {
      setCreating(false);
    }
  };

  const isFormValid = () => {
    return formData.name.trim() && 
           formData.description.trim() && 
           formData.personality.traits.length > 0;
  };

  const renderBasicConfig = () => (
    <div className="space-y-6">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Agent Name *
        </label>
        <input
          type="text"
          value={formData.name}
          onChange={(e) => handleInputChange('basic', 'name', e.target.value)}
          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
          placeholder="e.g. Customer Support Assistant"
        />
      </div>
      
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Description *
        </label>
        <textarea
          value={formData.description}
          onChange={(e) => handleInputChange('basic', 'description', e.target.value)}
          rows={4}
          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
          placeholder="Describe what your agent does and how it helps users..."
        />
      </div>
    </div>
  );

  const renderPersonalityConfig = () => (
    <div className="space-y-6">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Personality Traits *
        </label>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
          {personalityTraits.map(trait => (
            <button
              key={trait}
              onClick={() => handleTraitToggle(trait)}
              className={`px-3 py-2 rounded-lg border text-sm transition-colors ${
                formData.personality.traits.includes(trait)
                  ? 'bg-primary-100 border-primary-300 text-primary-800'
                  : 'bg-white border-gray-300 text-gray-700 hover:bg-gray-50'
              }`}
            >
              {trait}
            </button>
          ))}
        </div>
      </div>
      
      <div className="grid md:grid-cols-2 gap-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Tone</label>
          <select
            value={formData.personality.tone}
            onChange={(e) => handleInputChange('personality', 'tone', e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
          >
            <option value="professional">Professional</option>
            <option value="casual">Casual</option>
            <option value="formal">Formal</option>
            <option value="playful">Playful</option>
          </select>
        </div>
        
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">Response Style</label>
          <select
            value={formData.personality.responseStyle}
            onChange={(e) => handleInputChange('personality', 'responseStyle', e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
          >
            <option value="helpful">Helpful</option>
            <option value="detailed">Detailed</option>
            <option value="concise">Concise</option>
            <option value="conversational">Conversational</option>
          </select>
        </div>
      </div>
    </div>
  );

  const renderAppearanceConfig = () => (
    <div className="space-y-6">
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">Theme</label>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
          {themes.map(theme => (
            <button
              key={theme.id}
              onClick={() => handleInputChange('appearance', 'theme', theme.id)}
              className={`p-3 rounded-lg border text-sm transition-colors ${
                formData.appearance.theme === theme.id
                  ? 'border-primary-300 ring-2 ring-primary-500'
                  : 'border-gray-300 hover:border-gray-400'
              }`}
            >
              <div className={`w-6 h-6 rounded mx-auto mb-2 ${theme.color}`}></div>
              {theme.name}
            </button>
          ))}
        </div>
      </div>
      
      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Welcome Message
        </label>
        <input
          type="text"
          value={formData.appearance.welcomeMessage}
          onChange={(e) => handleInputChange('appearance', 'welcomeMessage', e.target.value)}
          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
          placeholder="Hello! How can I help you today?"
        />
      </div>
    </div>
  );

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h2 className="text-2xl font-bold text-gray-900">Create New Agent</h2>
        <p className="text-gray-600">Configure your AI agent's personality, knowledge, and behavior</p>
      </div>

      <div className="grid lg:grid-cols-3 gap-8">
        {/* Configuration Panel */}
        <div className="lg:col-span-2">
          <div className="bg-white rounded-lg card-shadow">
            {/* Tab Navigation */}
            <div className="border-b border-gray-200">
              <nav className="-mb-px flex space-x-8 px-6">
                {configTabs.map((tab) => (
                  <button
                    key={tab.id}
                    onClick={() => setActiveConfigTab(tab.id)}
                    className={`py-4 px-1 border-b-2 font-medium text-sm whitespace-nowrap transition-colors ${
                      activeConfigTab === tab.id
                        ? 'border-primary-500 text-primary-600'
                        : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                    }`}
                  >
                    {tab.name}
                    {tab.required && <span className="text-red-500 ml-1">*</span>}
                  </button>
                ))}
              </nav>
            </div>

            {/* Tab Content */}
            <div className="p-6">
              {activeConfigTab === 'basic' && renderBasicConfig()}
              {activeConfigTab === 'personality' && renderPersonalityConfig()}
              {activeConfigTab === 'appearance' && renderAppearanceConfig()}
              {activeConfigTab === 'knowledge' && (
                <div className="text-center py-12 text-gray-500">
                  Knowledge base configuration coming soon...
                </div>
              )}
              {activeConfigTab === 'behavior' && (
                <div className="text-center py-12 text-gray-500">
                  Behavior configuration coming soon...
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Preview Panel */}
        <div className="lg:col-span-1">
          <div className="bg-white rounded-lg card-shadow p-6 sticky top-8">
            <h3 className="text-lg font-medium text-gray-900 mb-4">Live Preview</h3>
            
            <div className="space-y-4">
              <div className="p-4 bg-gray-50 rounded-lg">
                <h4 className="font-medium text-gray-900">
                  {formData.name || 'Agent Name'}
                </h4>
                <p className="text-sm text-gray-600 mt-1">
                  {formData.description || 'Agent description...'}
                </p>
              </div>
              
              {formData.personality.traits.length > 0 && (
                <div>
                  <h5 className="text-sm font-medium text-gray-700 mb-2">Personality</h5>
                  <div className="flex flex-wrap gap-1">
                    {formData.personality.traits.map(trait => (
                      <span
                        key={trait}
                        className="px-2 py-1 bg-primary-100 text-primary-800 rounded-full text-xs"
                      >
                        {trait}
                      </span>
                    ))}
                  </div>
                </div>
              )}
              
              <div className="pt-4 border-t border-gray-200">
                <button
                  onClick={handleCreateAgent}
                  disabled={!isFormValid() || creating}
                  className={`w-full py-3 px-4 rounded-lg font-medium transition-colors ${
                    isFormValid() && !creating
                      ? 'bg-primary-600 text-white hover:bg-primary-700'
                      : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                  }`}
                >
                  {creating ? (
                    <div className="flex items-center justify-center">
                      <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                      Creating...
                    </div>
                  ) : (
                    'Create Agent'
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AgentCreator; 